
.personal-img {
  border-radius: 50%;
  border: 1px solid #DCDCDC;
}
body {
  padding: 0;
  margin: 0;
}
.clip-wp {
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  z-index: 11;
  background-color: #000;
  text-align: center;
}
.clip-wp #container {
  background-color: #000;
  text-align: center;
  width: 100%;
  left: 0;
  right: 0;
  top: 20px;
  bottom: 80px;
  margin: 0 auto;
  position: absolute;
}
.clip-wp #image-box {
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
  margin: auto;
}
.clip-wp #cover-box {
  position: absolute;
  z-index: 9999;
  display: none;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
  margin: auto;
}
#save-img {
  position: absolute;
  bottom: 20px;
  width: 90%;
  left: 5%;
}



