/* div左右滑动动画 */
/* 右滑 */
@-webkit-keyframes right
{
from {left:-375px;
}
to {left:0px;
}
}
@keyframes right
{
from {left:-375px;
}
to {left:0px;
}
}
/* 左滑 */
@-webkit-keyframes left
{
from {left:0px;
}
to {left:-375px;
}
}
@keyframes left
{
from {left:0px;
}
to {left:-375px;
}
}
a[data-v-05f8e08c]:-webkit-any-link {
    color:black;
}



